.MuiInputLabel-formControl {
  /* top: -6px; */
  left: 5px;
  position: absolute;
  transform: translate(0, 30px) scale(1);
}
.MuiInputLabel-shrink {
  transform: translate(0, -12.5px) scale(1);
  transform-origin: center;
  font-size: 11px;
  color: #000;
  font-weight: 600;
}

.MuiInput-underline {
  font-size: 16px;
  /* padding: 10px; */
  border: 2px solid #ddd;
  width: 100%;
  height: 45px;
  text-align: center;
  border-radius: 4px;
}

.MuiPickersCalendarHeader-iconButton {
  transform: rotate(180deg) !important;
}

.MuiFormControl-root {
  /* margin-right: 10px; */
  /* height: 100%; */
}
.MuiInput-underline:before {
  right: 0;
  left: 0;
  bottom: 0;
  position: absolute;
  border: none;
  pointer-events: none;
}
.MuiInput-underline:before ::-moz-focus-inner {
  right: 0;
  left: 0;
  bottom: 0;
  position: absolute;
  border: none;
  pointer-events: none;
}
label + .MuiInput-formControl {
  margin-top: 10px;
  border-radius: 4px !important;
  width: 100%;
  background-color: #fff;
}
.datepickerdate {
  background-color: #fff;
}
.starttime {
  width: 36%;
  background-color: #fff;
}

.MuiInput-underline:hover:not(.Mui-disabled):before {
  border-bottom: none;
}
.MuiPickersToolbar-toolbar {
  background-color: #04724d !important;
}
.MuiPickersClock-pin {
  background-color: #04724d !important;
}
.MuiPickersDay-daySelected {
  background-color: #04724d !important;
}
.MuiInput-underline:after {
  right: 0;
  left: 0;
  bottom: 0;
  position: absolute;
  transform: scaleX(0);
  transition: transform 200ms cubic-bezier(0, 0, 0.2, 1) 0ms;
  border: none;
  pointer-events: none;
}
.MuiPickersClockPointer-pointer {
  background-color: #04724d !important;
}

.MuiGrid-justify-content-xs-space-around {
  justify-content: end;
}
.MuiPaper-elevation1 {
  box-shadow: none !important;
}
.a {
  fill: none !important;
}
.MuiPickersClockNumber-clockNumberSelected {
  background-color: #04724d !important;
}
.MuiPickersTimePickerToolbar-hourMinuteLabelReverse {
  flex-direction: unset;
  padding-left: 10px;
}
.MuiPickersTimePickerToolbar-ampmSelection {
  display: flex;
  margin-right: 17px !important;
  margin-left: 30px !important;
  flex-direction: column;
}
.MuiButton-textPrimary {
  color: #04724d !important;
}

.MuiInputBase-root{
  height: 34px;
}
.MuiInputBase-input{
  font-size: 11px;
}

.jodit p{
  font-size: 11px;
}

.css-qc6sy-singleValue{
  font-size: 11px;
}
.MuiTypography-h6{
  font-size: 13px;
}
body{
  font-family: "Poppins", sans-serif;
}